import { USER_ADD } from '../actions';

// Esse reducer ser responsvel por tratar as informaes da pessoa usuria
const initialState = {
  email: '',
  password: '',
};

const userReducer = (state = initialState, action) => {
  switch (action.type) {
  case USER_ADD:
    return {
      ...state,
      [action.name]: action.payload,
    };
  default:
    return state;
  }
};

export default userReducer;
